import { element } from "protractor";
import { AuthService } from "./../../shared/user/auth.service";
import {
  Component,
  OnInit,
  ViewChild,
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Renderer2
} from "@angular/core";
import { MDBDatePickerComponent } from "./../../../typescripts/pro/date-picker/datepicker.component";

import { Router, ActivatedRoute } from "@angular/router";
import * as moment from "moment";
import { BehaviorSubject } from "rxjs/BehaviorSubject";
import { Observable } from "rxjs/Observable";
import { ActiveTaskService } from "../../shared/task/activetask.service";
import { Task } from "../../shared/task/task";

import { Template } from "../../shared/template/template";
import { TemplateService } from "../../shared/template/template.service";

import { UserService } from "../../shared/user/user.service";

import { Team } from "../../shared/team/team";
import { TeamStack } from "../../shared/team/team.stack";
import { TeamService } from "../../shared/team/team.service";
import { BasicFunctions } from "../../shared/utility/basic-functions";

interface Marker {
  lat: number;
  lng: number;
  label?: string;
  draggable: boolean;
}

@Component({
  selector: "app-tasks",
  templateUrl: "./activetask.component.html",
  styleUrls: ["./activetask.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ActiveTaskComponent implements OnInit, AfterViewInit {
  @ViewChild("taskViewModal") taskViewModal;
  @ViewChild("taskFormModal") taskFormModal;
  @ViewChild("taskDeleteModal") taskDeleteModal;
  @ViewChild("mapModal") mapModal;
  @ViewChild("locationInput") locationInput;
  @ViewChild("locationMap") locationMap;
  @ViewChild("taskForm") taskForm;
  @ViewChild("body") body;
  @ViewChild("datePicker") datePicker: MDBDatePickerComponent;

  public taskDetails: Task;
  public addTask: Task;
  public taskForDelete: Task;
  public taskForEdit: Task;
  public searchQuery: string;

  marker: Marker;
  lat: number;
  lng: number;
  zoom: number;

  public templates: BehaviorSubject<Array<any>>;
  public tasks: BehaviorSubject<Array<Task>>;
  public searchTerm: BehaviorSubject<String>;

  public options: Object;
  public taskCount: number;
  public templateForTask: Template;
  public users;
  public filteredUsers;
  public team: BehaviorSubject<Team>;
  public teamLeadName: string;
  public teamName: string;
  public teamMembers: Array<any>;
  public assignmentModes: Array<any>;
  public deviceOrAgentNumberPresent: boolean;
  public showDate: boolean;
  public showDuration: boolean;
  public startDate: any;
  public startTime: any;
  public duration: number;
  public durationType: string;
  public endDate: number;
  public showComments: boolean;
  public taskComments: Array<any>;
  public customerDetails;
  public createTeamId;
  public loggedInUserTeams: Array<Team>;

  public showAssigned: boolean;
  public showReviewer: boolean;

  public templateActive: boolean;
  public assignmentModeActive: boolean;
  public frequencyActive: boolean;
  public startDateActive: boolean;
  public startTimeActive: boolean;
  public durationTypeActive: boolean;
  public assigneeActive: boolean;
  public reviewerActive: boolean;
  public loggedInUser;
  public order: string;
  public templateLabels: Array<any>;

  selectedTemplate: string;
  selectedAssignmentMode: string;
  frequency: Array<any>;
  durationOptions: Array<any>;
  datePickerOptions: any;

  constructor(
    public taskService: ActiveTaskService,
    private userService: UserService,
    private templateService: TemplateService,
    private teamService: TeamService,
    public teamStack: TeamStack,
    private ref: ChangeDetectorRef,
    private authService: AuthService,
    public basicFunctions: BasicFunctions,
    private router: Router,
    private route: ActivatedRoute
  ) {
    this.taskDetails = new Task();
    this.addTask = new Task();
    this.taskForDelete = new Task();
    this.taskForEdit = new Task();
    this.tasks = new BehaviorSubject([]);
    this.templates = new BehaviorSubject([]);
    this.options = {};
    this.team = new BehaviorSubject(new Team());
    this.teamLeadName = "";
    this.createTeamId = "";
    this.teamMembers = [];
    this.teamName = "";
    this.taskCount = 0;
    this.frequency = [];
    this.filteredUsers = [];
    this.assignmentModes = [{ value: "Manual", label: "Manual" }];
    this.templateForTask = new Template();
    this.showDate = false;
    this.showDuration = true;
    this.showComments = false;
    this.taskComments = [];
    this.showAssigned = false;
    this.showReviewer = false;
    this.loggedInUserTeams = new Array(new Team());

    this.startDate = "2018-12-12";
    this.templateActive = false;
    this.assignmentModeActive = false;
    this.frequencyActive = false;
    this.startDateActive = false;
    this.startTimeActive = false;
    this.durationTypeActive = false;
    this.assigneeActive = false;
    this.reviewerActive = false;
    this.order = "title";
    this.loggedInUser = this.authService.getUser();
    this.templateLabels = [];
  }

  ngOnInit() {
    this.teamStack.reset();
    this.lat = 6.426013463543917;
    this.lng = 3.4273771048901835;
    this.zoom = 10;
    this.marker = {
      lat: 6.426013463543917,
      lng: 3.4273771048901835,
      label: "You",
      draggable: false
    };
    this.generateTaskCards();

    this.teamService.loggedInUserDownlines.subscribe(data => {
      this.users = data.map(elem => {
        return {
          value: elem,
          label: this.getUserNameByUserId(elem)
        };
      });
    });

    this.teamService.loggedInUserTeams.subscribe(data => {
      this.loggedInUserTeams = data;
      if (this.loggedInUserTeams[0]) {
        this.addTask.teamId = this.loggedInUserTeams[0].teamId;
      }
      this.createTeamId = data[0] ? data[0].teamId : "";
    });

    this.teamStack.leadTeams.subscribe(data => {
      if (data && data[0]) {
        this.teamLeadName = this.getUserNameByUserId(data[0].teamLead);
        this.teamName = data
          .map(elem => {
            return elem.teamName;
          })
          .join(", ");
      }
    });

    this.teamStack.teamMembers.subscribe(data => {
      const leadId = this.teamStack.leadTeams.value[0] ? this.teamStack.leadTeams.value[0].teamLead : "";
      this.teamMembers = Array.from(data);
      this.teamMembers = this.teamMembers.filter(v => v !== leadId);
    });

    this.templateService.templates.subscribe(data => {
      this.templates.next(
        data.map(v => {
          return { label: v.name, value: v.templateId };
        })
      );
    });

    this.tasks.subscribe(data => {
      this.taskCount = data.length;
    });

    this.frequency = [
      { value: "Anytime", label: "Anytime" },
      { value: "One off", label: "One off" },
      { value: "Weekly", label: "Weekly" },
      { value: "Monthly", label: "Monthly" },
      { value: "Quarterly", label: "Quarterly" }
    ];

    this.datePickerOptions = {};
    this.showNotificationTask();
  }

  ngAfterViewInit() { }

  toggleComments() {
    this.showComments = !this.showComments;
  }

  log($event) {
    console.log($event);
  }

  // map view
  setCurrentPosition() {
    if ("geolocation" in navigator) {
      navigator.geolocation.getCurrentPosition(position => {
        this.lat = position.coords.latitude;
        this.lng = position.coords.longitude;
        this.zoom = 12;

        this.marker = {
          lat: position.coords.latitude,
          lng: position.coords.longitude,
          label: "You",
          draggable: false
        };
        this.locationMap.triggerResize(false);
      });
    }
  }

  watchPosition() {
    if ("geolocation" in navigator) {
      navigator.geolocation.watchPosition(position => {
        this.lat = position.coords.latitude;
        this.lng = position.coords.longitude;
        this.zoom = 10;

        this.marker = {
          lat: position.coords.latitude,
          lng: position.coords.longitude,
          label: "You",
          draggable: false
        };
        this.locationMap.triggerResize(false);
      });
    }
  }

  showMap() {
    this.mapModal.show();
    setTimeout(() => {
      this.locationMap.triggerResize(false);
    }, 500);
  }

  mapClicked($event: any) {
    this.marker.lat = $event.coords.lat;
    this.marker.lng = $event.coords.lng;
  }

  markerDragEnd($event: any) {
    this.marker.lat = $event.coords.lat;
    this.marker.lng = $event.coords.lng;
  }

  getAddress() {
    this.taskService.getAddress(this.marker.lat, this.marker.lng).subscribe();
    setTimeout(() => {
      this.addTask.location.address = this.taskService.address;
      this.mapModal.hide();
      this.locationInput.nativeElement.focus();
    }, 1000);
  }
  // end map view

  computePrice(task) {
    const currency = task.template.currency || "",
      taskCost = task.cost;
    const totalCost = Object.keys(taskCost).reduce(function (sum, value) {
      return sum + Number(taskCost[value]);
    }, 0);
    return currency + " " + totalCost;
  }

  searchValueChange(q) {
    this.searchQuery = q;
    this.taskService.searchTerm.next(this.searchQuery);
  }

  sort(q) {
    this.taskService.sortBy(q);
  }

  getUserNameByUserId(userId) {
    return this.userService.getUserNameByUserId(userId);
  }

  getCustomerPhoneNumber(task) {
    if (
      task.templateFieldsValues &&
      task.templateFieldsValues.priorTemplate &&
      task.templateFieldsValues.priorTemplate.customerPhoneNumber
    ) {
      return task.templateFieldsValues.priorTemplate.customerPhoneNumber;
    }
    return "N/A";
  }

  getCustomerName(task) {
    if (
      task.templateFieldsValues &&
      task.templateFieldsValues.priorTemplate &&
      task.templateFieldsValues.priorTemplate.customerName
    ) {
      return task.templateFieldsValues.priorTemplate.customerName;
    }
    return "N/A";
  }

  getScheduledTime(task, time) {
    if (task.templateFieldsValues && task.templateFieldsValues.priorTemplate) {
      return task.templateFieldsValues.priorTemplate[time] || 0;
    }
    return 0;
  }

  onTemplateChange(templateId) {
    this.selectedTemplate = templateId;
    this.options = {};

    const templates = this.templateService.templates.value.filter(v => {
      return v.templateId === templateId;
    });

    if (templates.length > 0) {
      this.templateForTask = templates[0];
      this.addTask.template = this.templateForTask;

      this.addTask.template.priorTemplate.map(v => {
        v.dropDownValues = v.dropDownValues
          .map(q => {
            const keys = Object.keys(q);
            if (keys.length > 0) {
              return { value: keys[0], label: q[keys[0]] };
            } else {
              return {};
            }
          })
          .filter(q => {
            return Object.keys(q).length > 0;
          });
      });
      this.assignmentModes = this.templateForTask.assignmentMode.map(v => {
        return { value: v, label: v };
      });
    }
    this.ref.detectChanges();
  }

  onAssignmentChange(assignmentMode) {
    const userId = this.authService.getUserId();
    if (assignmentMode) {
      switch (assignmentMode) {
        case "Broadcast":
        case "Team":
          this.showAssigned = false;
          this.showReviewer = false;
          this.addTask.reviewer = userId;
          break;

        case "Manual Assignment":
        case "Auto Assignment":
          this.showAssigned = true;
          this.showReviewer = false;
          break;
        default:
          this.showAssigned = true;
          this.showReviewer = true;
      }
    }
  }

  onAssignedChange(assignedTo) {
    if (assignedTo) {
      this.filteredUsers = this.users.filter(v => {
        return v.value !== assignedTo;
      });
      this.showReviewer = true;
    }
  }

  onFrequencyChange(freq) {
    const allDurationOptions = [
      { value: "hours", label: "Hours" },
      { value: "days", label: "Days" },
      { value: "weeks", label: "Weeks" },
      { value: "months", label: "Months" }
    ];
    switch (freq) {
      case "Anytime":
        this.showDate = false;
        break;
      case "One off":
        this.showDate = true;
        this.durationOptions = allDurationOptions;
        break;
      case "Weekly":
        this.showDate = true;
        this.durationOptions = allDurationOptions.slice(0, 2);
        break;
      case "Monthly":
        this.showDate = true;
        this.durationOptions = allDurationOptions.slice(0, 3);
        break;
      case "Quarterly":
        this.showDate = true;
        this.durationOptions = allDurationOptions;
        break;
      default:
        break;
    }
  }

  onDurationChange(dur) {
    switch (dur) {
      case "hours":
        this.endDate = this.duration * 3600000;
        break;

      case "days":
        this.endDate = this.duration * 86400000;
        break;

      case "weeks":
        this.endDate = this.duration * 604800000;
        break;

      case "months":
        this.endDate = this.duration * 2629800000;
        break;

      default:
        break;
    }
  }

  formatComment(comment) {
    return `${comment.cause} - ${comment.comment} -- ${this.convertDateTime(comment.date, "DD MMMM YYYY")}`;
  }

  viewTaskDetails(task) {
    this.taskComments = task.comments
      ? task.comments.sort((a, b) => {
        if (a.date > b.date) {
          return -1;
        }
        if (a.date < b.date) {
          return 1;
        }
        return 0;
      }) || []
      : [];
    this.showComments = false;
    this.taskDetails = Object.assign(new Task(), task);
    this.taskViewModal.show();
    this.ref.detectChanges();
  }

  showNotificationTask() {
    this.route.queryParams.subscribe(params => {
      if (params && params.id) {
        const refId = params.id;
        const sub = this.taskService.fetchDone.subscribe(data => {
          if (data) {
            this.taskService.getOneTask(refId).subscribe(taskData => {
              const task = taskData.body;
              if (task && task["data"] && task["data"][refId]) {
                this.viewTaskDetails(task["data"][refId]);
              }
            }, err => {
              setTimeout(() => this.userService.showError("Task Unavailable/Deleted"));
              this.router.navigate(["/home/tasks/active"]);
            }, () => {
              if (sub) {
                sub.unsubscribe();
              }
            });
          }
        });
      }
    });
  }

  generateTaskCards(id = this.authService.getUserId()) {
    const sub = this.teamService.getTeamsByLead(id, this.taskService.fetchDone).subscribe();
    this.taskService.getDownlineTasks().subscribe();
    this.tasks = this.taskService.downlineTasks;
    sub.unsubscribe();
  }

  createTask(task) {
    const momTime = moment(`${this.startTime}`, ["h:mmA"]).format("HH:mm");
    const SDT = Date.parse(`${this.startDate}T${momTime}`);

    task.templateFieldsValues.priorTemplate.startDateTime = SDT;
    task.templateFieldsValues.priorTemplate.duration = this.endDate;
    task.template = this.templateForTask;

    const userId = this.authService.getUserId();
    task.assigner = task.assigner ? task.assigner : userId;
    task.teamId = task.teamId ? task.teamId : this.createTeamId;
    this.taskService.create(task);

    this.taskFormModal.hide();
    this.taskViewModal.hide();
    this.addTask = new Task();
    this.showDate = false;
    this.startDate = null;
    this.startTime = null;
    this.duration = null;
    this.durationType = "";
    this.endDate = null;
    this.selectedTemplate = "";
    this.taskForm.reset();
  }

  closeViewModal() {
    const routeModal = this.route.queryParams.subscribe(params => {
      if (params && params.id) {
        this.taskViewModal.hide()
        this.router.navigate(["/home/tasks/active"]);
      } else {
        this.taskViewModal.hide()
      }
      if (routeModal) {
        routeModal.unsubscribe();
      }
    });
  }

  closeModal() {
    this.taskFormModal.hide();
    this.addTask = new Task();
    this.showDate = false;
    this.startDate = null;
    this.startTime = null;
    this.duration = null;
    this.durationType = "";
    this.endDate = null;
    this.selectedTemplate = "";
    this.taskForm.reset();
  }

  confirmDelete(task) {
    this.taskForDelete = Object.assign(new Task(), task);
    this.taskDeleteModal.show();
  }

  deleteTask(task) {
    this.taskService.deleteTask(task);
    this.taskDeleteModal.hide();
    this.taskViewModal.hide();
  }

  convertDateTime(dateTime, format) {
    if (dateTime === 0 || dateTime === undefined) {
      return "-";
    }
    return moment(new Date(dateTime)).format(format);
  }

  getDropdownArray(dropdownValues, i) {
    if (!this.options[i]) {
      this.options[i] = dropdownValues;
    }
  }

  openTaskCreateModal() {
    this.taskForm.reset();
    this.taskFormModal.show();
  }

  openEditTaskModal(task) {
    if (this.checkEditStatus(task)) {
      this.showDuration = false; // under revision
      this.addTask = Object.assign(new Task(), task);
      this.selectedTemplate = this.addTask.template.templateId;
      const d = new Date(task.templateFieldsValues.priorTemplate.startDateTime);
      const date = this.basicFunctions.getDateFormat(d, "-");
      if (this.datePicker) {
        setTimeout(() => this.datePicker.onUserDateInput(date), 0);
      }
      this.startTime = moment(task.templateFieldsValues.priorTemplate.startDateTime).format("hh:mmA");
      this.taskFormModal.show();
    }
  }

  checkEditStatus(task) {
    if (
      task.status === "Assigned" &&
      (task.assigner === this.loggedInUser.userId || this.loggedInUser.permissions.corona_web_task_mgt)
    ) {
      return true;
    }
    return false;
  }

  editTask(task) {
    if (this.checkEditStatus(task)) {
      this.taskService.updateTask(task);

      this.taskFormModal.hide();
      this.taskViewModal.hide();
      this.addTask = new Task();
      this.showDate = false;
      this.startDate = null;
      this.startTime = null;
      this.duration = null;
      this.durationType = "";
      this.endDate = null;
      this.selectedTemplate = "";
      this.taskForm.reset();
    }
  }

  sortBy(type) {
    this.order = type;
  }
}



